import Footer from "./components/Footer";
import Header from "./components/Header";
import Question from "./components/Question";
import MCQ4 from "./components/MCQ4";
import Splash from "./components/Splash";
import UserForm from "./components/UserForm";
import Coupon from "./components/Coupon";
import OtpInput from 'react18-otp-input';
import { useSelector, useDispatch } from "react-redux";
import { Fade, Slide, Grow } from "@mui/material";
import Rating from '@mui/material/Rating';
import { useState } from "react";
import { decremented, incremented } from './store/store';
import { useNavigate } from "react-router-dom";
import { Button } from "@mui/material";
import './App.css'


function App() {

    const pageIndex = useSelector((state) => state.pageIndex);
    let progress = pageIndex * 100 / (questions.length + 1);
    const commonQuestion = 'Rate your overall experience'
    const [rating, setRating] = useState(0);
    const [otp, setOtp] = useState('');
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [disablePointer, setDisablePointer] = useState(false);
    const decrement = () => {
        dispatch(decremented());
    }
    const increment = () => {
        dispatch(incremented());
    }
    onpopstate = () => {
        decrement();
    };

    return (
        <div className="App">

            {pageIndex < questions.length + 3 &&
                <Header logo={brandLogo}></Header>
            }


            {pageIndex == 0 && <Splash></Splash>}
            {pageIndex < questions.length+1 && pageIndex > 0 &&
                <Fade in>
                    <div>
                        <Question question={questions[pageIndex-1].label}></Question>
                        <MCQ4 options={questions[pageIndex-1].options}></MCQ4>
                    </div>
                </Fade>
            }
            {pageIndex == questions.length+1 &&
                <div>
                    <Question question={commonQuestion}></Question>
                    <div className="fiveStar">
                        <Rating style={{ pointerEvents: disablePointer ? 'none' : 'auto' }}
                            name="star-rating"
                            size="large"
                            value={rating}
                            onChange={(event, newValue) => {

                                setDisablePointer(true);
                                setRating(newValue);
                                setTimeout(() => increment(), 600);
                                setTimeout(() => setDisablePointer(false), 600);
                            }}
                        />
                    </div>
                </div>
            }
            {pageIndex == questions.length + 2 &&

                <Fade in>
                    <div>
                        <UserForm></UserForm>
                    </div>
                </Fade>
            }

            {pageIndex == questions.length + 3 &&
                <Fade in>
                    <div className="otp-container">
                        <div className="otp-text">
                            Please enter your OTP
                        </div>

                        <OtpInput
                            inputStyle="inputStyle"
                            numInputs={4}
                            onChange={(value) => setOtp(value)}
                            isInputNum={true}
                            shouldAutoFocus
                            value={otp}
                        />
                        <Button onClick={increment} variant="contained">Verify</Button>
                    </div>
                </Fade>
            }
            {pageIndex == questions.length + 4 &&
                <Slide in direction="up" timeout={1000}>
                    <div className="coupon-screen">
                        <Coupon
                            brandImage={coupounBrandImage}
                            brandColor={brandColor}
                            industryIcon={industryIcon}
                            industry={industry}
                            couponCode={couponCode}
                            validity={validity}
                            couponDetails={couponDetails}
                        />
                    </div>
                </Slide>
            }

            {pageIndex < questions.length + 2 &&

                <Footer progress={progress}
                    FooterContent={pageIndex < questions.length + 2}
                    Questions={questions.length + 2 - pageIndex}
                    Counter={pageIndex > 0}
                />
            }
        </div>
    );
}



export default App;


